import React, { useState, Suspense } from 'react'
// import Expenses from "./components/Expenses/Expenses";
// import NewExpenses from "./components/NewExpenses/NewExpenses";

const Expenses = React.lazy(() => import('./components/Expenses/Expenses'))
const NewExpenses = React.lazy(() => import('./components/NewExpenses/NewExpenses'))

const INITIAL_EXPENSES = [
  {
    id: "e1",
    title: "Toilet Paper",
    amount: 94.12,
    date: new Date(2020, 7, 14),
  },
  { id: "e2", title: "New TV", amount: 799.49, date: new Date(2021, 2, 12) },
  {
    id: "e3",
    title: "Car Insurance",
    amount: 294.67,
    date: new Date(2021, 2, 28),
  },
  {
    id: "e4",
    title: "New Desk (Wooden)",
    amount: 450,
    date: new Date(2021, 5, 12),
  },
];

function App() {
  const [expenses, setExpenses] = useState(INITIAL_EXPENSES)
  
  const addExpenseDataHandler = (enteredExpenseData) => {
    setExpenses((prevExpenses) => {
      return [enteredExpenseData, ...prevExpenses]
    })
}
  
  return (
    <Suspense fallback={<div>Loading...</div>}>
    <div>
      <NewExpenses onAddExpenseData={addExpenseDataHandler}/>
      <Expenses item={expenses} />
    </div>
    </Suspense>
  );
}

export default App;
